@import "./_variables.scss";
@import "./_themes.scss";
@import "./_mixin.scss";
//样式重写，这里其实就跟ant design vue的base.less基本一致，所以这里重写也覆盖预览页面，这是基础样式，所有组件都以此样式为基础
body{
    margin: 0;
    padding: 0;
    color: rgba(0,0,0,.65);
    font-size: 14px;
    font-variant: tabular-nums;
    line-height: 1.4;
    background-color: #fff;
    font-feature-settings: "tnum";
}
* {
    font-family: pingfang SC, helvetica neue, arial, hiragino sans gb, microsoft yahei ui, microsoft yahei, simsun, sans-serif;
}
input::-ms-clear,
input::-ms-reveal {
  display: none;
}
*,
*::before,
*::after {
  box-sizing: border-box; // 1
}
@-ms-viewport {
    width: device-width;
}

// IE10+ doesn't honor `<meta name="viewport">` in some cases.
@-ms-viewport {
    width: device-width;
}

// Shim for "new" HTML5 structural elements to display correctly (IE10, older browsers)
article,
aside,
dialog,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section {
    display: block;
}
img{
    vertical-align: middle;
    border-style: none;
}
.idm_module{
    position: relative;
}
//主题循环类
@each $themename,$theme in $platformThemes {
    .#{$themename}{
        //按钮
        .el-button--primary{
            background-color:  map-get($map: $theme, $key: "mainColor") !important;
            border-color:  map-get($map: $theme, $key: "mainColor") !important;
            &:hover{
                opacity: 0.9;
            }
        }
        .el-button--default{
            &:hover{
                background-color:  rgba(map-get($map: $theme, $key: "colorR"),map-get($map: $theme, $key: "colorG"),map-get($map: $theme, $key: "colorB"),0.05) !important;
                border-color:  rgba(map-get($map: $theme, $key: "colorR"),map-get($map: $theme, $key: "colorG"),map-get($map: $theme, $key: "colorB"),0.2) !important;
                color: map-get($map: $theme, $key: "mainColor") !important;
            }
        }
    }
    
    .confirmButtonClass-#{$themename}{
        background-color: map-get($map: $theme, $key: "mainColor") !important;
        border-color:map-get($map: $theme, $key: "mainColor") !important;
    }
}